/* harmony default export */ __webpack_exports__["a"] = ({
  hero: {
    title: 'About the Toolkit',
    description: 'This Toolkit is designed to present ways that individuals and groups can overcome\n      challenges in implementing Article 19 of the WHO FCTC and bringing claims against the tobacco industry to court.'
  },

  notes: {
    title: 'Notes on the scenarios',
    items: ['They have been identified as the most viable ways of taking legal action against the tobacco industry.', 'They are represented as decision trees, asking key questions about your legal system.', 'Relevant sources are included, such as key cases, legislation, best practices from other fields, and reform options for holding the industry to account.']
  },

  input: {
    title: 'Give us your Input',
    items: ['This Toolkit is a \'living\' resource that will continually evolve as legal systems develop and tobacco litigation progresses around the world.', 'When using it, if you encounter any errors in case descriptions or laws from your juristriction,\n        or if you are aware of successful cases or laws that might qualify as a best practice,\n        please contact <a href="mailto:fctcsecretariat@who.int">fctcsecretariat@who.int</a>.']
  },

  why: {
    title: 'Why you need the toolkit',
    description: {
      firstP: 'Many governments, health care providers and victims of smoking-related disease have\n        strong claims against the tobacco industry. But tobacco litigation has proved challenging\n        in the past with claimants facing technical and legal obstacles, and legal systems being\n        unable to redress the power imbalance between the industry and those it has harmed.',
      secondP: 'That said, there have been examples of litigation success in the Americas,\n        Europe and Australasia. There are also innovative best practices from other legal\n        fields that can be specifically adapted to tobacco litigation. This Toolkit aims\n        to gather and curate this information for users, as well as to guide them towards\n        implementing Article 19 in the most appropriate way for their situation.'
    },
    quote: 'Article 19 is clear that legal action to establish liability is an important part of tobacco control.\n        But actions taken to address the harm caused by the tobacco industry are often without precedent and legal systems need to adapt."'
  },

  how: {
    title: 'How the Toolkit was developed',
    description: 'The WHO FCTC Article 19 Civil Liability Toolkit was developed by the expert group\n      on liability and adopted by the Conference of the Parties (COP) to the WHO FCTC at the 7th\n      Session of the COP held in Delhi, India from 7 to 12 November 2017. The expert group on\n      liability was established at the 5th Session of the COP held in Seoul, Republic of Korea from\n      12 to 17 November 2012 and was mandated to:',
    item: {
      one: '<strong>Identify, examine and collect existing best practices</strong> for civil and criminal liability including compensation.',
      two: '<strong>Identify obstacles to effective action in the areas of civil and criminal liability,</strong> (particularly civil), including compensation, and to provide potential solutions.',
      three: '<strong>Identify options for developing legislation</strong> in the areas of civil and criminal liability (particularly for civil liability).',
      four: '<strong>Provide options for technical support</strong>, international cooperation and information exchange for the effective implementation of Article 19.'
    },
    card: 'The Expert Group\u2019s mandate was continued in 2014, and in 2016,\n      the establishment of this Toolkit was proposed as a practical resource to help stakeholders\n      choose the most appropriate measures for strengthening their implementation of Article 19.'
  },

  FCTC: {
    title: 'About the FCTC',
    description: 'The WHO Framework Convention on Tobacco Control (WHO FCTC) is an evidence-based\n      treaty that reaffirms the right of all people to the highest standard of health.\n      The Convention represents a milestone for the promotion of public health and provides\n      new legal dimensions for international health cooperation, requiring\n      Parties to adopt multi-sectoral tobacco control strategies.',
    items: ['Article 4 recognises, as one of the Guiding Principles, that issues relating to liability are an important part of comprehensive tobacco control.', 'Article 19 states that Parties shall consider taking legislative action or promoting their existing laws, where necessary, to deal with criminal and civil liability, including compensation where appropriate.'],
    cta: 'FCTC Website'
  },

  links: {
    title: 'Links and Notes',
    items: ['The substantive legal information upon which the Toolkit is based is contained in document FCTC/ COP/6/8, in particular, Annexes 1-3.', 'Some aspects of the scenarios may be more appropriate in certain jurisdictions according to their own legal systems and traditions, and the resources available to the Parties in implementing Article 19.', 'This Toolkit is a product of the Convention Secretariat in collaboration with the Expert Group on Article 19, Dr. Andrew Higgins of the University of Oxford, Johanna Gusman, M.Sc., J.D. as a Visiting Researcher at the University of Oxford, Lushomo Information Design Agency and accredited observers to the WHO FCTC.']
  }
});

