      secondP: '话虽如此，在美洲、欧洲和澳大拉西亚一直都有诉讼成功的例子。其他法律领域也有可专门适用于烟草诉讼的创新最佳实践。本工具包旨在为用户收集和整理这方面的信息，并指导他们以最适合其情况的方式执行第19条。'
