    description: '《世界卫生组织烟草控制框架公约》是一项以证据为基础的条约，重申人人享受最高而能获致之健康标准的权利。该公约是促进公共卫生的一个里程碑，为开展国际卫生合作提供了新的法律，并要求缔约方采取多部门烟草控制战略。',
    items: ['作为指导原则之一，第4条承认，与责任相关的问题是全面烟草控制的重要组成部分。', '第19条规定，必要时，各缔约方应考虑采取立法行动或促进其现有法律，以处理刑事和民事责任，适当时包括赔偿。'],
